import NewsAPI from "newsapi";
import { createContext, useEffect, useState } from "react";
import LoadingSpinner from "./components/LoadingSpinner";
import Layout from "./layout";
import Home from "./pages/Home";

export const QueryContext = createContext();
export const NewsContext = createContext();

function App() {
  const [query, setQuery] = useState({ category: "", pageSize: 10 });
  const [news, setNews] = useState([]);
  const [isFetching, setIsFetching] = useState(false);

  // 5062043d55da442088930dd755a43ea5
  // pub_9603c4270151a432e3636f2c1e414b7f5e69

  const newsApi = new NewsAPI(process.env.REACT_APP_API_KEY, {
    corsProxyUrl: "https://cors-anywhere.herokuapp.com/",
  });

  const fetchNews = async () => {
    try {
      const res = await fetch(
        "https://newsdata.io/api/1/news?apikey=pub_9603c4270151a432e3636f2c1e414b7f5e69&page=10"
      );
      const res2 = await res.json();
      console.log(res2);
    } catch (error) {
      console.log(error);
    }
  };

  const url = `https://newsapi.org/v2/top-headlines?country=us&apiKey=${process.env.REACT_APP_API_KEY}&category=${query.category}&pageSize=${query.pageSize}&page=1`;

  useEffect(() => {
    fetchNews();
    setIsFetching(true);
    // const request = new Request(url);

    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setNews(data.articles);
        setIsFetching(false);
      })
      .catch((err) => console.log(err));
  }, [query, url]);

  return (
    <QueryContext.Provider value={[query, setQuery]}>
      <NewsContext.Provider value={news}>
        <Layout>{isFetching ? <LoadingSpinner /> : <Home />}</Layout>
      </NewsContext.Provider>
    </QueryContext.Provider>
  );
}

export default App;
